@import url(https://fonts.googleapis.com/css2?family=Major+Mono+Display&family=Space+Mono:ital,wght@0,400;0,700;1,400;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{font-family:"Space Mono",monospace}button{font-family:"Space Mono",monospace}h1,h2,h3,h4,h5,h6,nav{font-family:"Major Mono Display",monospace}*[role=button]{cursor:pointer}.dashboard{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-flex-wrap:nowrap;flex-wrap:nowrap;justify-items:stretch;height:100vh}.dashboard header{-webkit-flex:0 0 auto;flex:0 0 auto;background-color:#282C34;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;color:white;padding:32px;height:40px}.dashboard header a{text-decoration:none;color:white}.dashboard header h1{margin:0}.dashboard header nav{font-size:1.5em;margin-left:64px}.dashboard header nav ol{margin:0;padding:0;list-style-type:none}.dashboard header nav ol li{display:inline;padding:0 0 0 32px;margin-left:48px;position:relative}.dashboard header nav ol li.current::before{content:'▻';position:absolute;left:0;line-height:24px}.dashboard header div:last-child{-webkit-flex:1 0 auto;flex:1 0 auto;text-align:right}.dashboard>div{-webkit-flex:1 0 auto;flex:1 0 auto;height:calc(100% - 104px)}.dashboard>div.overview{display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around;padding:32px;-webkit-align-items:flex-start;align-items:flex-start}.dashboard>div.overview>div{-webkit-flex:0 0 20%;flex:0 0 20%;border:1px solid #282C34;padding:16px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}.dashboard>div.overview>div h2{-webkit-flex:0 0 auto;flex:0 0 auto;margin:0}.dashboard>div.overview>div ul{list-style-type:none;padding:0;-webkit-align-self:stretch;align-self:stretch}.dashboard>div.two-panel{display:-webkit-flex;display:flex;-webkit-align-items:stretch;align-items:stretch}.dashboard>div.two-panel h2{margin-left:16px}.dashboard>div.two-panel>div.tabs-panel{padding:0;margin:0;-webkit-flex:0 0 35%;flex:0 0 35%;border-right:1px solid #282C34;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:stretch;align-items:stretch}.dashboard>div.two-panel>div.tabs-panel .tabs-container{display:-webkit-flex;display:flex;height:42px}.dashboard>div.two-panel>div.tabs-panel .tabs-container>.tab{-webkit-flex:1 1 auto;flex:1 1 auto;border:1px solid white;background-color:#d3d3d3;cursor:pointer;-webkit-flex-wrap:wrap;flex-wrap:wrap;padding:8px 16px}.dashboard>div.two-panel>div.tabs-panel .tabs-container>.tab.current{pointer-events:none;background-color:dimgray;color:white}.dashboard>div.two-panel>div.tabs-panel>.tab-content{padding:16px;height:calc(100% - 42px);overflow-y:auto}.dashboard>div.two-panel>div.tabs-panel>.tab-content .upgrade{margin-bottom:8px;border:1px solid #d3d3d3;padding:8px;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-flex-wrap:nowrap;flex-wrap:nowrap}.dashboard>div.two-panel>div.tabs-panel>.tab-content .upgrade>div.upgrade-image{-webkit-flex:0 0 auto;flex:0 0 auto;margin-right:20px;border:1px solid blue;background-color:whitesmoke;width:100px;height:100px}.dashboard>div.two-panel>div.tabs-panel>.tab-content .upgrade>div:not(.upgrade-image){-webkit-flex:1 1 auto;flex:1 1 auto}.dashboard>div.two-panel>div.tabs-panel>.tab-content .upgrade .upgrade-title{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}.dashboard>div.two-panel>div.tabs-panel>.tab-content .upgrade .upgrade-title h3{margin:4px 0}.dashboard>div.two-panel>div.tabs-panel>.tab-content .upgrade p{margin:4px 0}.dashboard>div.two-panel>div.tabs-panel>.tab-content .upgrade.unavailable{pointer-events:none;color:#d3d3d3}.dashboard>div.two-panel>div.tabs-panel>.tab-content .upgrade.expensive{pointer-events:none;color:red}.dashboard>div.two-panel>div.main-panel{-webkit-flex:1 0 auto;flex:1 0 auto;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.dashboard>div.two-panel>div.main-panel button{-webkit-align-self:center;align-self:center}.dashboard>div.two-panel>div.main-panel>div:last-child{-webkit-flex:1 0 auto;flex:1 0 auto;border-top:1px solid #282C34;margin-top:16px;padding:16px;height:calc(100% - 399px);overflow-y:scroll}.dashboard>div.two-panel #factory{width:350px;height:350px;border:1px solid blue;background:whitesmoke}

